
import AsyncStorage from '@react-native-async-storage/async-storage';

import { AUTH_KEY } from './keys';
import { ValueOf } from './types';

export const storeToSecureStorage = async (key: ValueOf<typeof AUTH_KEY>, data: string) => {
  await AsyncStorage.setItem(key, data);
}

export const getValueFromSecureStorage = async (key: ValueOf<typeof AUTH_KEY>) => {
  return new Promise<string | null>((resolve) => {
    AsyncStorage.getItem(key)
      .then(res => {
        if (res !== null) {
          resolve(res);
        } else {
          resolve(null);
        }
      })
      .catch(err => {
        resolve(null);
      });
  });
}

export const resetStorage = async () => {
  await AsyncStorage.removeItem(AUTH_KEY.token);
}