import { Dispatch } from '@reduxjs/toolkit';
import React, { useEffect, useState } from 'react';
import { ActivityIndicator, StyleSheet, View } from 'react-native';

import { AUTH_KEY } from '../constants/keys';
import { getValueFromSecureStorage } from '../constants/storage';
import { setToken } from '../reducers/authSlice';

interface AuthProviderProps {
  children: React.ReactNode,
  dispatch: Dispatch,
}

export default function AuthProvider({
  children,
  dispatch,
}: AuthProviderProps) {
  const [redhydrating, setRedhydrating] = useState(true);

  useEffect(() => {
    prepareAppState();
  }, []);

  const prepareAppState = async () => {
    const token = await getValueFromSecureStorage(AUTH_KEY.token);

    if (!!token) {
      dispatch(setToken(token));
    }

    setRedhydrating(false);
  };

  if (redhydrating) return (
    <View
      style={styles.container}
    >
      <ActivityIndicator />
    </View>
  );

  return children;
}

const styles = StyleSheet.create({
  container: {
    flex: 1,
    alignItems: 'center',
    justifyContent: 'center',
  },
});